import * as React from 'react';
import {lazy} from "react";

import Box from '@mui/material/Box';

import CssBaseline from '@mui/material/CssBaseline';
import BottomNavigation from '@mui/material/BottomNavigation';
import BottomNavigationAction from '@mui/material/BottomNavigationAction';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import VpnKeyIcon from '@mui/icons-material/VpnKey';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import AccountBalanceWalletIcon from '@mui/icons-material/AccountBalanceWallet';
import Paper from '@mui/material/Paper';



const Profile = lazy(() => import('./Pages/Profile'))
const ServiceList = lazy(() => import('./Pages/Services'))
const BuyService = lazy(() => import('./Pages/BuyService'))
const Wallet = lazy(() => import('./Pages/Wallet'))

export default function Home() {
    const [BNvalue, setBNvalue] = React.useState(3);

    const [ProfileActive , setProfileActive] = React.useState(1);
    const [ServicesActive , setServicesActive] = React.useState(0);
    const [BuyServicesActive , setBuyServicesActive] = React.useState(0);
    const [WalletActive , setWalletActive] = React.useState(0);

    const ref = React.useRef(null);
    const [UserId,setUserId] = React.useState(window.Telegram.WebApp.initDataUnsafe.user.id)
    //const [UserId,setUserId] = React.useState(5661569137)

    React.useEffect(() => {
        try {
            setUserId(window.Telegram.WebApp.initDataUnsafe.user.id)
        }catch (e) {
            window.alert("Cant read!")
            window.alert(e)
        }
        try {
            window.Telegram.WebApp.expand(true);
        }catch (e) {
            console.log(e)
        }
    }, [window.Telegram.WebApp]);

    React.useEffect(() => {
        setProfileActive(0);
        setServicesActive(0);
        setBuyServicesActive(0);
        setWalletActive(0);
        if (BNvalue === 3)
            setProfileActive(1);
        if (BNvalue === 2)
            setServicesActive(1);
        if (BNvalue === 1)
            setBuyServicesActive(1);
        if (BNvalue === 0)
            setWalletActive(1);
    }, [BNvalue]);



    return (
        <Box sx={{ pb: 7 }} ref={ref}>
            <CssBaseline />
            {ProfileActive ?
                <Profile user_id={UserId} active={ProfileActive} />
                :
                <></>
            }
            {ServicesActive ?
                <ServiceList user_id={UserId} active={ServicesActive} />
                :
                <></>
            }
            {BuyServicesActive ?
                <BuyService user_id={UserId} active={BuyServicesActive} />
                :
                <></>
            }
            {WalletActive ?
                <Wallet user_id={UserId} active={WalletActive}/>
                :
                <></>
            }




            <Paper sx={{ position: 'fixed', bottom: 0, left: 0, right: 0 }} elevation={3}>
                <BottomNavigation
                    showLabels
                    value={BNvalue}
                    onChange={(event, newValue) => {
                        setBNvalue(newValue);
                    }}
                >
                    <BottomNavigationAction label="کیف پول" icon={<AccountBalanceWalletIcon />} />
                    <BottomNavigationAction label="خرید سرویس" icon={<ShoppingCartIcon />} />
                    <BottomNavigationAction label="سرویس ها" icon={<VpnKeyIcon />} />
                    <BottomNavigationAction label="پروفایل" icon={<AccountCircleIcon />} />
                </BottomNavigation>
            </Paper>
        </Box>
    );
}
